import { useState } from "react";
import { useEffect } from "react";
import "./css/style.css";
import Newsitem from "./NewsItem";
import Card from "./Card";

const Newspage = () => {
  const [news, setNews] = useState([]);
  

  useEffect(() => {
    const fetchApi = async () => {
      const url = `https://newsapi.org/v2/top-headlines?country=in&apiKey=f398cee6cf1f4a8bad34a051bafdaffa`;
      const response = await fetch(url);
      const responseData = await response.json();
      if (response.ok) {
        console.log(responseData);
        console.log(responseData.articles[0].title);
        const loadedNews=[];
        for(const key in responseData.articles){
            loadedNews.push({
                id:key,
                image:responseData.articles[key].urlToImage,
                title:responseData.articles[key].title,
                content:responseData.articles[key].content,
                author:responseData.articles[key].author,
            })
        }
        setNews(loadedNews);
      console.log(loadedNews);
    }
};
fetchApi();
}, []);

const actionHandler=()=>{
    console.log(news[0].image);

}

    const newsList= news.map((newss)=>(
        <Newsitem
        key={newss.id}
        id={newss.id}
        image={newss.image}
        title={newss.title}
        content={newss.content}
        author={newss.author}

    />
    ))

  return (
    <>
    <Card>
    {newsList}
    </Card>
   
    {/* <button onClick={newsList} >Click</button> */}
     
    </>
  );
};
export default Newspage;
